@import '~assets/styles/h3Imports';

.root {
  overflow: visible;
  width: 664px;
  @media #{$large-down} {
    width: 472px;
  }

  @media #{$small-down} {
    width: calc(100% - 2rem);
  }
}

.mobile-landscape {
  overflow: auto;
}

.button {
  @media #{$large-down} {
    width: 100% !important;
  }
}

.title {
  justify-content: center;
  text-align: center;
}

.paragraph {
  text-align: center;
}

[data-modal-section='body'] {
  overflow: visible;
}
